<template>
  <div class="takeColorBox" :style="boxStyle"></div>
</template>

<script>
export default {
  name: "TakeColorBox",
  props: [
    'bgColor', 'height','width'
  ],
  data(){
    return{
      boxStyle: {
        backgroundColor: this.bgColor,
        height: this.height,
        width: this.width
      },
    }
  }

}
</script>

<style lang="scss" scoped>
.takeColorBox{
  border: 1px solid black;
  cursor: pointer;
}
</style>