<template>
<div class="show-drawing-grid" :style="[gridBorder?{}:{borderColor: 'white'}]">
  <SinglePixel v-for="i in 2500" :key="i" :bgColor="bgColor" :gridBorder="gridBorder" :massAssign="massAssign" />
</div>
</template>

<script>

import SinglePixel from "@/components/SinglePixel";
export default {
  name: "ShowDrawingGrid",
  props : [
    'bgColor','gridBorder'
  ],
  data(){
    return{
      massAssign: false,
    }
  },
  components: {
    SinglePixel
  },
  computed: {
    findDimension: function(){
      return 10;
    }
  },
  methods: {
    toggleBorderEmit(){
      this.$emit('toggle-border');
    },
    eraserModeEmit(){
      this.$emit('eraser-mode');
    }
  },
  created(){
    window.addEventListener('keyup',ev => {
      if(ev.key === 'u'){
        this.massAssign = !this.massAssign;
        this.$emit('toggle-manual');
      }
      else if(ev.key === 't'){
        this.toggleBorderEmit();
      }
      else if(ev.key === 'e'){
        this.eraserModeEmit();
      }
    });
  },
}
</script>

<style lang="scss" scoped>

.show-drawing-grid{
  margin: 0 auto;
  height: 500px;
  width: 500px;
  border: 1px solid black;
  border-right: none;
  border-bottom: none;
  display: grid;
  grid-template-rows: repeat(50, 2fr);
  grid-template-columns: repeat(50, 2fr);
}
.pixel-row{
  height: 100%;
  width: 100%;
  border: 1px solid black;
  border-left: none;
  border-top: none;
}
</style>