<template>
<div class="single-pixel"
     @mouseover="mouseoverBackgroundChange"
     @click="clickedBackgroundChange"
     :style="[gridBorder?
     {background: backgroundColorInside}:
     {background: backgroundColorInside, borderColor: backgroundColorInside}]"
>

</div>
</template>

<script>
export default {
  name: "SinglePixel",
  props: [
      'bgColor','gridBorder','massAssign'
  ],
  data(){
    return{
      backgroundColorInside: 'white',
      borderColor: 'black',
    }
  },
  methods: {
    mouseoverBackgroundChange(){
      if(this.massAssign === true){
        this.backgroundColorInside = this.bgColor;
      }
    },
    clickedBackgroundChange(){
      this.backgroundColorInside = this.bgColor;
    }
  }
}
</script>

<style scoped>
.single-pixel{
  height: 100%;
  width: 100%;
  border: 1px solid black;
  border-left: none;
  border-top: none;
}
</style>