<template>
  <div class="main">
    <ShowEditorial v-if="canIShowEditorial" @close-button-clicked="closeEditorial()" />

    <div class="left">
      <p class="editorial-btn">
        <button class="btn btn-success show-editorial-btn" @click="showEditorial()">Editorial</button>

        <button v-if="!assignMode" class="btn btn-success show-color-mode">Manual</button>
        <button v-if="assignMode" class="btn btn-success show-color-mode">Automatic</button>

        <button v-if="colorMode" class="btn btn-success show-erase-mode">Color</button>
        <button v-if="!colorMode" class="btn btn-success show-erase-mode">Eraser</button>

        <button v-if="gridBorder" class="btn btn-success show-border-mode">Border On</button>
        <button v-if="!gridBorder" class="btn btn-success show-border-mode">Border Off</button>
      </p>
<!--      <br>-->
      <div class="grid">
        <ShowDrawingGrid :bgColor="colorCode" :gridBorder="gridBorder"
                         @toggle-border="toggleBorder()"
                         @eraser-mode="eraserMode()"
                         @toggle-manual="toggleManual()"/>
      </div>
    </div>
    <div class="right">
      <div class="color-picker with-border">
        <label for="colorCode" class="label">Pick a color : &nbsp; &nbsp;</label>
        <input id="colorCode" type="color" v-model="colorCode" @change="changedColorPicker"/>
      </div>
      <div class="current-color with-border">
        Current Color : &nbsp; <div :style=" { background: colorCode }"></div>
      </div>
      <div class="last-5-colors with-border padding-sm">
        My last used colors:
        <div class="colors">
<!--          <TakeColorBox v-for="col in lastFiveColors" class="color" :key="col" :bgColor="col" height="40px" width ="40px"></TakeColorBox>-->
          <div v-for="col in lastFiveColors" :key="col" class="color">
            <TakeColorBox :bgColor="col" height="20px" width ="20px" @click="ClickedTakeColorBox(col)" />
          </div>
        </div>
      </div>
<!--      <div class="some-common-colors with-border padding-sm">-->
<!--        Some Standard Colors:-->
<!--        <div class="colors">-->
<!--          <div v-for="col in standardColors" :key="col" class="color">-->
<!--            <TakeColorBox :bgColor="col" height="20px" width ="20px" @click="ClickedAddColor(col)" />-->
<!--          </div>-->
<!--        </div>-->
<!--      </div>-->
    </div>
  </div>
</template>

<script>


import TakeColorBox from "@/components/TakeColorBox";
import ShowDrawingGrid from "@/components/ShowDrawingGrid";
import ShowEditorial from "@/components/ShowEditorial";

export default {
  name: "MainBody",
  components: {
    TakeColorBox,ShowDrawingGrid, ShowEditorial
  },
  data(){
    return{
      colorCode: '#ffffff',
      gridBorder: true,
      colorMode: true,
      assignMode: false,
      canIShowEditorial: false,
      lastFiveColors: [
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff',
        '#ffffff','#ffffff','#ffffff','#ffffff','#ffffff'
      ],
      standardColors: [
        'Pink','DeepPink','MediumVioletRed',
        'MediumOrchid','DarkOrchid','BlueViolet', 'DarkMagenta','Purple','blue','MediumSlateBlue','SlateBlue','DarkSlateBlue','Indigo',
          'LightSalmon','IndianRed','Crimson','Red','FireBrick','DarkRed','Orange','OrangeRed','Gold','Yellow','Khaki','DarkKhaki','GreenYellow','Like','LightGreen','LimeGreen','SpringGreen','MediumSeaGreen','ForestGreen','Green','DarkGreen','YellowGreen','OliveDrab','LightSeaGreen','DarkCyan','Teal','Aqua','Cyan','MediumBlue','DarkBlue','Blue','Navy','MidnightBlue','SaddleBrown','Brown','Maroon',
          'white','whitesmoke','Gray','DimGray','DarkStateGray','Black','silver'
      ],
    }
  },
  methods: {
    ClickedTakeColorBox(col){
      this.colorCode = col;
    },
    changedColorPicker(e){
      if(this.lastFiveColors[0] !== e.target.value){
        this.lastFiveColors.pop();
        this.lastFiveColors.unshift(e.target.value);
      }
    },
    ClickedAddColor(col){
      this.colorCode = col;
      if(this.lastFiveColors[0] !== col){
        this.lastFiveColors.pop();
        this.lastFiveColors.unshift(col);
      }
    },
    showEditorial(){
      this.canIShowEditorial = true;
    },
    closeEditorial(){
      this.canIShowEditorial = false;
    },
    toggleBorder(){
      this.gridBorder = !this.gridBorder;
    },
    eraserMode(){
      this.colorMode = !this.colorMode;
      if(this.colorCode === '#ffffff'){
        this.colorCode = this.lastFiveColors[0];
      } else {
        this.colorCode = '#ffffff';
      }
    },
    toggleManual(){
      this.assignMode = !this.assignMode;
    }
  },

}
</script>

<style lang="scss" scoped>

.btn{
  cursor: pointer;
  margin-left: 20px;
  height: 35px;
  width: 80px;
  border-radius: 0;
  border: none;
  color: white;
  &:focus, &:active{
    outline: none;
  }
}
.show-color-mode{
  background: darkred;
  &:hover{
    background: rgba(43,123,124,1);
  }
}
.show-erase-mode{
  background: navy;
  &:hover{
    background: rgba(43,123,124,1);
  }
}
.show-border-mode{
  background: limegreen;
  &:hover{
    background: rgba(43,123,124,1);
  }
}

.show-editorial-btn{
  background: darkgreen;
  &:hover{
    background: rgba(43,123,124,1);
  }
}
.main{
  margin-bottom: 10px;
  gap: 10px;
  display: flex;
  flex-wrap: wrap;
  .left{
    margin-top: 10px;
    width: 65%;
    .editorial-btn{
      text-align: center;
    }
    p{
      margin-bottom: 10px;
    }
  }
  .grid{
    width: 100%;
    align-items: center;
  }
  .right{
    width: calc(35% - 30px);
    .with-border{
      margin-top: 20px;
      box-shadow: 0 0 10px #3C3C3C;
      border-radius: 5px;
    }
  }
  .current-color{
    height: 70px;
    display: flex;
    //justify-content: center;
    padding-left: 10px;
    align-items: center;
    //font-weight: bolder;
    div{
      //display: inline;
      height: 50px;
      width: 50px;
      border-radius: 50%;
      border: 2px solid black;
    }
  }
  .color-picker{

    height: 100px;
    display: flex;
    //justify-content: center;
    align-items: center;
    padding-left: 10px;
    input[type="color"] {
      -webkit-appearance: none;
      border: none;
      width: 150px;
      height: 40px;
      cursor: pointer;
      &::-webkit-color-swatch-wrapper {
        padding: 0;
      }
      &::-webkit-color-swatch {
        border: 1px solid black;
      }
    }
    label{
      color: black;
      //font-weight: bolder;
    }
  }
  .last-5-colors{
    .colors{
      margin-top: 10px;
      margin-bottom: 10px;
      //height: 30px;
      //background: red;
      display: flex;
      flex-wrap: wrap;
      //border-left: 1px solid black;
      gap: 10px;
      .color{
        cursor: pointer;
      }
    }
  }
  .some-common-colors{
    //height: 120px;
    .colors{
      margin-top: 10px;
      display: flex;
      gap: 10px;
      flex-wrap: wrap;
    }
  }
}
.padding-sm{
  padding: 10px;
}
@media only screen and(max-width: 850px) {
  .main{
    flex-direction: column;
    .left{
      min-width: 100%;
    }
    .right{
      width: 96%;
      margin: 0 auto;
    }
  }
}

</style>